<template>
    <div>
        <el-card class="header">
            <el-form :inline="true" class="headerForm" :model="searchHeaderForm">
                <el-form-item><el-input clearable v-model="searchHeaderForm.find" placeholder="请输入名称"></el-input>
                </el-form-item>
                <el-button type="primary" @click="getDatas">查询</el-button>
            </el-form>
        </el-card>
        <el-card>
            <el-table :data="tableData" border style="width: 100%"
                :header-cell-style="{ background: '#F3F4F7', color: '#555' }">
                <el-table-column prop="title_name" label="作业名称" min-width="200">
                </el-table-column>
                <el-table-column prop="course_name" label="课程" min-width="200">
                </el-table-column>
                <el-table-column label="作业状态" min-width="200">
                    <template #default="scope">
                        <el-tag type="danger" size="small" v-if="nowTime < scope.row.start_time">
                            未开始</el-tag>
                        <el-tag type="success" size="small"
                            v-if="nowTime > scope.row.start_time && nowTime < scope.row.end_time">
                            进行中</el-tag>
                        <el-tag type="warning" size="small" v-if="nowTime > scope.row.end_time">
                            已结束</el-tag>
                    </template>
                </el-table-column>
                <el-table-column label="是否提交" min-width="200">
                    <template #default="scope">
                        <el-tag type="danger" size="small" v-if="scope.row.isUpload == false">
                            未提交</el-tag>
                        <el-tag type="success" size="small" v-if="scope.row.isUpload != false">
                            已提交</el-tag>

                    </template>
                </el-table-column>
                <el-table-column prop="start_time" label="开始时间" min-width="200">
                </el-table-column>
                <el-table-column prop="end_time" label="结束时间" min-width="200">
                </el-table-column>
                <el-table-column align="center" fixed="right" label="操作" width="200">
                    <template #default="scope">
                        <el-button type="primary" @click="showCourseDetails(scope.row._id)">详情</el-button>
                    </template>
                </el-table-column>
            </el-table>
            <div v-if="tableData.length !== 0" style="display: flex; flex-direction: row-reverse; margin-top: 10px">
                <el-pagination :current-page="currentPage" :page-size="pageSize" :total="total"
                    :page-sizes="[10, 20, 50, 100]" background layout="total, sizes, prev, pager, next, jumper"
                    @size-change="handleSizeChange" @current-change="handleCurrentChange" />
            </div>
        </el-card>
    </div>
</template>
<script>
import {
    work_find,
    course_join
} from '@/api/student/api.js'
import moment from 'moment';
// eslint-disable-next-line no-unused-vars
export default {
    data() {



        return {
            nowTime: moment().format('YYYY-MM-DD HH:mm:ss'),
            tableData: [],
            // 控制收缩
            isCollapse: false,

            search: "",
            operaDialog: false,
            dialogTitle: "",
            pageSize: 10, // 分页的页面大小
            total: 0, // 分页的数据总数
            currentPage: 1, // 分页的当前页
            opera: "",
            operaForm: {
                name: "",
                department: "",
                grade: "",
            },
            //查询
            searchHeaderForm: {
                find: '',
            },
            options: [],

        }
    },
    created() {
        this.getDatas()
    },
    methods: {
        desearch() {
            this.searchHeaderForm = {
                find: '',
            },
                this.getDatas()
        },
        handleData(handle, row) {
            if (handle == "add") {
                this.operaForm = {}
                this.dialogTitle = "添加API"
                this.opera = "add"
            } else if (handle == "update") {
                this.operaForm = { ...row } //!!! 直接赋值是引用类型,引起表单重置问题
                this.dialogTitle = "编辑API"
                this.opera = "update"
            }
            this.operaDialog = true
        },
        handleSizeChange(value) {
            // 改变页面大小
            this.pageSize = value
            this.getDatas()
        },
        handleCurrentChange(value) {
            // 改变页数
            this.currentPage = value
            this.getDatas()
        },
        async getDatas() {
            const params = {
                page: this.currentPage,
                page_size: this.pageSize,
                search: this.searchHeaderForm.find,
            }
            const res = await work_find(params)
            if (res.data.code === 200) {
                if (res.data.data) {
                    this.total = res.data.data.count
                    if (res.data.data.data == null) {
                        this.tableData = []
                    } else {
                        this.tableData = res.data.data.data
                    }
                } else {
                    this.total = res.data.data.count
                    this.tableData = []
                }
            }
        },
        showCourseDetails(data) {
            this.$router.push({
                path: '/student/workDetails',
                query: { work_id: data }
            })

        },
        resetForm() {
            this.$refs.operaForm.clearValidate()
            this.operaForm = {},
                this.operaDialog = false
        },
    },
};
</script>
  
<style lang="less" scoped>
.header {
    margin-bottom: 20px;

    .searchBtn {
        float: right;
    }

    .headerForm {
        padding: 0 !important;

        .el-form-item {
            margin-bottom: 0px;
        }
    }
}

.upload-demo {
    display: flex;
    flex-flow: column nowrap;
    justify-content: center;
    align-items: center;
}

.el-date-editor.el-input {
    width: 100%;
}

.fileLink {
    color: #f8f8f8;
    text-decoration: none;
}

.el-form {
    padding: 0 100px;
}

/deep/ .operaDialog .el-form-item__label {
    padding: 0 25px 0 0;
}
</style>
  